body {
  margin: 0;
  box-sizing: border-box;
}

.stars {
  display: flex;
}

.stars__star {
  width: 16px;
  height: 16px;
  background-image: url('./images/star.svg');
  background-position: center;
  background-repeat: no-repeat;
  margin-right: 4px;
}

.stars__star--active {
  background-image: url('./images/star-active.svg');
}
